
*{
  margin: 0;
  padding: 0;
}
a{
  text-decoration: none;
}
body{
  background: #f8f8f9;
}
.header{
  height: 48px;
  line-height: 48px;
  background: rgba(0,0,0,0.8);
  color: #fff;
}
.header-title{
  padding: 0 32px;
  float: left;
  color: #fff;
}
.header-menu{
  float: right;
  margin-right: 32px;
}
.header-menu-cart{
  color: #fff;
}
.header-menu-cart span{
  display: inline-block;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 50%;
  background: #ff5500;
  color: #fff;
  font-size: 12px;
}


.product-not-found{
  text-align: center;
  padding: 32px;
}
.list-control{
  background: #fff;
  border-radius: 6px;
  margin: 16px;
  padding: 16px;
  -webkit-box-shadow: 0 1px 1px rgba(0,0,0,.2);
          box-shadow: 0 1px 1px rgba(0,0,0,.2);
}
.list-control-filter{
    margin-bottom: 16px;
}
.list-control-order-item,
.item-control-filter-item,
.item-control-order-item
{
    cursor: pointer;
    display: inline-block;
    border: 1px solid #e9eaec;
    border-radius: 4px;
    margin-right: 6px;
    padding: 2px 6px;
}
.list-control-order-item.on,
.item-control-filter-item.on,
.item-control-order-item.on
{
   background: #f2352e;
   border: 1px  solid #f2352e;
   color: #fff;
}


.product[data-v-0f7b2ecb]{
    width: 25%;
    float: left;
}
.product-main[data-v-0f7b2ecb]{
    display: block;
    margin: 16px;
    padding: 16px;
    border: 1px solid #dddee1;
    border-radius: 6px;
    overflow: hidden;
    background: #fff;
    text-align: center;
    position: relative;
}
.product-main img[data-v-0f7b2ecb]{
    width: 100%;
}
h4[data-v-0f7b2ecb]{
    color: #222;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.product-main:hover h4[data-v-0f7b2ecb]{
    color: #0070C9;
}
.product-main:hover .product-add-cart[data-v-0f7b2ecb]
{
    display: inline-block;
}
.product-color[data-v-0f7b2ecb]{
    display: block;
    width: 16px;
    height: 16px;
    border: 1px solid #dddee1;
    border-radius: 50%;
    margin: 6px auto;
}
.product-cost[data-v-0f7b2ecb]{
    color: #de4037;
    margin-top: 6px;
}
.product-add-cart[data-v-0f7b2ecb]{
    display: none;
    padding: 4px 8px;
    background: #2d8cf0;
    color: #fff;
    font-size: 12px;
    border-radius: 3px;
    cursor: pointer;
    position: absolute;
    top: 5px;
    right: 5px;
}

.product{
    margin: 32px;
    padding: 32px;
    background: #fff;
    border: 1px solid #dddee1;
    border-radius: 10px;
    overflow: hidden;
}
.product-image{
    width: 50%;
    height: 550px;
    float: left;
    text-align: center;
}
.product-image img{
    height: 100%;
}
.product-info{
    width: 50%;
    padding: 150px 0 250px;
    height: 150px;
    float: left;
    text-align: center;
}
.product-cost{
    color: #f2352e;
    margin: 8px 0;
}
.product-add-cart{
    display: inline-block;
    padding: 8px 64px;
    margin: 8px 0;
    background: #2d8cf0;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
}
.product-desc{
    background: #fff;
    margin: 32px;
    padding: 32px;
    border: 1px solid #dddee1;
    border-radius: 10px;
    text-align: center;
}
.product-desc img{
    display: block;
    width: 50%;
    margin: 32px auto;
    padding: 32px;
    border-bottom: 1px solid #dddee1;
}

